.favorite-container {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.favorite-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
}

.favorite-icon svg {
  transition: opacity 0.2s ease;
}

.favorite-container:hover .favorite-icon svg {
  opacity: 0.8;
}

/* Remove any default margins from Streamlit */
[data-testid="stVerticalBlock"] > div:has(> [data-testid="element-container"] > .stFavorite) {
  margin-left: 0 !important;
  margin-right: 0 !important;
}